<script>
  import { cart } from "../stores.js";
  const removeItem = (product) => {
    for (let item of $cart) {
      if (item.id === product.id) {
        if (product.quantity > 1) {
          product.quantity -= 1;
          $cart = $cart;
        } else {
          $cart = $cart.filter((cartItem) => cartItem != product);
        }
        return;
      }
    }
  };
  const addItem = (product) => {
    for (let item of $cart) {
      if (item.id === product.id) {
        product.quantity += 1;
        $cart = $cart;
        return;
      }
    }
  };
  $: total = $cart.reduce((sum, item) => sum + item.price * item.quantity, 0);
</script>

<div class="cart-list">
  {#each $cart as item}
    {#if item.quantity > 0}
      <div class="cart-item">
        <img width="50" src={item.image} alt={item.name} />
        <div>
          {item.quantity} <button on:click={() => addItem(item)}>+</button>
          <button on:click={() => removeItem(item)}>-</button>
        </div>
        <p>${item.price * item.quantity}</p>
      </div>
    {/if}
  {/each}
  <div class="total">
    <h4>Total: $ {total}</h4>
  </div>
</div>

<style lang="css">
  .cart-list {
    margin-top: 1rem;
    border: 1.5px solid;
    padding: 50px;
    width: 15rem;
    
    margin-bottom: 1rem;
  }

  h4 {
    border-bottom: 1px solid #d7d7d7;
    padding: 21px;
    margin: 0;
  }

  .cart-item {
    display: grid;
    grid-template-columns: 50px 100px 50px 30px 30px 50px;
    border-bottom: 1px solid #d7d7d7;
    padding: 10px;
  }
  div > button {
    height: 25px;
  }

  .total {
    text-align: right;
  }
</style>
