import { readable, writable, derived } from "svelte/store";
import * as myjson from "./public/stock.json";

let products = readable(Object.values(myjson.default));

let cart = writable([]);
let totalprice = derived(cart, ($cart) => {
  let price = 0;

  price = $cart.reduce((sum, item) => sum + item.price * item.quantity, 0);
  return price;
});

let totalitems = derived(cart, ($cart) => {
  let items = 0;

  $cart.forEach((e) => (items += e.quantity));
  return items;
});

export { cart, products, totalprice, totalitems };
